//global style
@font-face {
	font-family: Montserrat Arabic;
	src: url(../webfonts/Montserrat-Arabic-Regular.ttf);
}

.main-wrapper {
	width: 100%;
	overflow: hidden;
}

body {
	font-family: "Montserrat", Montserrat Arabic, sans-serif;

	.main-wrapper {
		background-color: #fff;
		width: 100%;
	}
}

//global style

//header
header {
	padding: 20px 0;
	background-color: $main_color;

	.content {
		display: flex;
		justify-content: center;
		width: 100%;

		.navbar-brand {
			display: block;
			margin: 0;

			img {
				max-width: 270px;
			}
		}
	}
}

//header

//main section
section.hero {
	padding: $padding_section;

	.content {
		text-align: center;
		margin: 2rem 0 4rem;

		h1 {
			font-size: 5rem;
			font-weight: 700;
			max-width: 770px;
			margin: 2rem auto 4rem;

			span {
				color: $main_color;
			}
		}

		.button {
			a {
				img {
					width: 50px;
				}
			}
		}
	}
}

//main section

//works section
section.works {
	.content {
		.item {
			img {
				width: 100%;
				height: 750px;
				object-fit: cover;
				object-position: center;
				cursor: pointer;
			}
		}
	}
	.owl-theme {
		.owl-nav {
			margin-top: 0;

			.owl-prev,
			.owl-next {
				position: absolute;
				top: 50%;
				color: $main_color;
				border: 2px solid $main_color;
				width: 35px;
				height: 35px;
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 50%;
				font-weight: 700;

				&:hover {
					background-color: transparent;
				}
			}
			.owl-prev {
				left: 0;
			}
			.owl-next {
				right: 0;
			}
		}
		.owl-dots {
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 15px;
			// position: absolute;
			// bottom: 0;
			// width: 100%;

			.owl-dot {
				span {
					width: 14px;
					height: 14px;
					opacity: 0.5;
					background-color: $main_color;
				}
				&.active {
					span {
						width: 20px;
						height: 20px;
						opacity: 1;
					}
				}
			}
		}
	}
}
//works section

//cta section
section.cta {
	padding: $padding_section;

	.content {
		text-align: center;

		h2 {
			font-size: 1.3rem;
			max-width: 600px;
			margin: auto;
		}

		.button {
			a {
				color: $main_color;
			}
		}
	}
}
//cta section

//footer
footer {
	padding: 30px 0;
	background-color: $main_color;

	.content {
		text-align: center;

		.footer-links {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;

			a {
				color: #fff;
				margin: 0 5px;
			}
		}
	}
}

//footer
